import React from 'react';
import { Button, Box } from "@mui/material";
import Web3 from "web3";
import {ContractContext} from '../assets/abi/farm-contract';
import {ContractContext as TokenContractContext} from '../assets/abi/busd';


interface ConnectButtonProps {
  style?: React.CSSProperties;
}

class ConnectButton extends React.Component<ConnectButtonProps> {
 
  
  constructor(props: ConnectButtonProps) {
      super(props);

  }

  componentDidMount = () => { 
      this.handleConnectWallet();
  }

  componentWillUnmount = () => { 

  }

  initialConnect = () => {
    if (window.ethereum) {
      window.ethereum
        .request({ method: "eth_requestAccounts" })
        .then((res: string[]) => {
          if (res.length >= 1) {
            window.connected = true;
            window.walletAddress = res[0];
            this.handleConnectWallet();
          }
        });
    } else if (
      window.confirm(
        "You have to use MetaMask or TrustWallet to connect. Press OK to continue to the MetaMask download page. https://metamask.io/download"
      )
    ) {
      let a = document.createElement("a");
      a.target = "_blank";
      a.href = "https://metamask.io/download";
      a.click();
    }
  }

  handleConnectWallet = () => {

    if(window.ethereum){
      window.web3 = new Web3(window.ethereum);

      window.ethereum.request({ method: "eth_accounts" }).then((res: any) => {
        if (res.length >= 1) {
          window.connected = true;
          window.walletAddress = res[0];
          this.updateRefLink();
          //console.log("eth_accounts", res);
          this.forceUpdate();

          window.farm_contract = (new window.web3.eth.Contract(window.contract_abi, window.contractAddress) as unknown) as ContractContext;
          window.token_contract = (new window.web3.eth.Contract(window.token_abi, window.tokenAddress) as unknown) as TokenContractContext;
        }
      });
    }
    
  }

  handleDeactivateAccount = () => {
    window.connected = false;
    window.walletAddress = "";
    this.forceUpdate();
  }

  updateRefLink = () => {
    let refLinkWalledId = document.getElementById("walletAddressRefLink");
    if(refLinkWalledId != null){
      refLinkWalledId.innerText = window.walletAddress;
    }
  }
  

  render = () => {

    let button;
    if (window.connected) { 
      button = <Button variant="contained" onClick={this.handleDeactivateAccount}>disconnect</Button>;    
    } else {
      button = <Button variant="contained" onClick={this.initialConnect}>connect</Button>;  
    }

    return (
      <Box>
        <div>
          {window.walletAddress &&
            `${window.walletAddress.slice(0, 6)}...${window.walletAddress.slice(
              window.walletAddress.length - 4,
              window.walletAddress.length
            )}`}
        </div>
        <div style={{padding: "16px 0 16px 0"}}>
          {button}
        </div>
        
      </Box>
      );
    
  }
}

export default ConnectButton;