import React from 'react';
import { StandardTextFieldProps, TextField } from '@mui/material';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { formatEthFromWei, formatNumber } from '../Formatters';
import BUSDfarm from "../assets/img/busd-logo-medium.png";
import Collapse from '@mui/material/Collapse';
import { styled } from '@mui/material/styles';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import TelegramIcon from '@mui/icons-material/Telegram';
import IconButton from '@mui/material/IconButton';


interface PlanProps {
  label?: string;
  percentage: number;
  days: number;
  planIndex: number;
  minInvest: number;
  style?: React.CSSProperties;
  description: string;
  nonVipMinInvest: number;
  completeDisabled?: boolean;
}

const bull = (
    <Box
      component="span"
      sx={{ display: 'inline-block', mx: '2px', transform: 'scale(0.8)' }}
    >
      •
    </Box>
  );

  const ExpandMore = styled((props) => {
    const { expand, ...other }: any = props;
    return <IconButton {...other} />;
  })(({ theme, expand }: any) => ({
    transform: !expand ? 'rotate(0deg)' : 'rotate(180deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  }));


interface Deposit {
  amount: string; //"1000000000000000000"​​
  finish: string; //"2498928189"				​​
  percent: string; //"50"				​​
  plan: string; //"0"				​​
  start: string; //"1634928189"
}
  
const promisify = (inner: any) =>
	new Promise((resolve, reject) =>
		inner((res: any, err: any) => {
			if (err) {
				reject(err)
			} else {
				resolve(res);
			}
		})
);



class Plan extends React.Component<PlanProps> {

  public label? : string;
  public percentage: number;
  public days: number;
  public minInvest: number;
  public planIndex: number;
  public nonVipMinInvest: number;
  public description: string;
  public completeDisabled?: boolean;

  private labelNode: React.RefObject<HTMLSpanElement>;
  private summaryNode: React.RefObject<HTMLSpanElement>;
  private amountInput: React.RefObject<StandardTextFieldProps>;
  private currentWalletBalance: number = 0;

  private withdrawTax: number = 25;

  state = {
    disabled: false,
    balance: "0",
    readyForPayout: "0",
    reinvestPossible: false,
    depositsTxt: "",
    expanded: false
  }

  _stateInterval: any;

  constructor(props: PlanProps) {
      super(props);

      this.labelNode = React.createRef();
      this.summaryNode = React.createRef();
      this.amountInput = React.createRef();

      // properties
      this.percentage = props.percentage;
      this.days = props.days;
      this.label = props.label;
      this.minInvest = props.minInvest;
      this.planIndex = props.planIndex;
      this.description = props.description;
      this.nonVipMinInvest = props.nonVipMinInvest;
      this.completeDisabled = props.completeDisabled;

      //  scope hitch
      //this.onChange_Amount = this.onChange_Amount.bind(this);
      //this.onClick_Invest = this.onClick_Invest.bind(this);
      //this.checkState = this.checkState.bind(this);
  }

  handleExpandClick = () => {
    this.setState({expanded: !this.state.expanded});
    this.getAllUserDeposits();
  }

  componentDidMount = () => { 
    this.calcROI();

    this.checkState();
    if(this._stateInterval){
      clearInterval(this._stateInterval);
    }
    this._stateInterval = setInterval(this.checkState, window.dataRefreshInterval);
  }

  componentWillUnmount = () => { 

  }

  checkRequirementsForInvest = () => {
    if(window.connected !== true){
      return;
    }

    switch(this.planIndex){
      case 0: // Basic 1
        if( this.amountInput.current != null ){
          if((new Number(this.amountInput.current.value as string) >= this.minInvest)){
              this.setState({
                disabled: false
              });
          }else{
            this.setState({
              disabled: true
            });
          }
        }
        break;
      case 1: // Basic 2
        if( this.amountInput.current != null ){
          if((new Number(this.amountInput.current.value as string) >= this.minInvest)){
              this.setState({
                disabled: false
              });
          }else{
            this.setState({
              disabled: true
            });
          }
        }
        break;
      case 2: // VIP 1 HUSTLER
      case 3: // VIP BALLER

        // My Invest in NonVIP
        //
        window.farm_contract.methods.getUserActiveNonVIPInvestments(window.walletAddress).call().then((result) => {
          let myNonVIPInvestmentAmount = parseFloat(window.web3.utils.fromWei(result));
          //console.log("myNonVIPInvestmentAmount", myNonVIPInvestmentAmount);
          //console.log("this.nonVipMinInvest", this.nonVipMinInvest);

          if( this.amountInput.current != null ){
            if((new Number(this.amountInput.current.value as string) >= this.minInvest)){
              //console.log("myNonVIPInvestmentAmount >= this.nonVipMinInvest", myNonVIPInvestmentAmount >= this.nonVipMinInvest);
              if(myNonVIPInvestmentAmount >= this.nonVipMinInvest){
                this.setState({
                  disabled: false
                });
              }else{ //.. disabled
                this.setState({
                  disabled: true
                });
              }
            }else{
              this.setState({
                disabled: true
              });
            }
          }

        }).catch((err:any) => {
          console.log(err);
        });


        break;
    }
  }

  checkState = () => {  
    if(window.connected !== true){
      this.setState({
        disabled: true
      });

      return;
    }

    if(this.completeDisabled === true){
      this.setState({
        disabled: true
      });
      return;
    }

    if( this.amountInput.current != null ){

      if(!(new Number(this.amountInput.current.value as string) >= this.minInvest)){
        this.setState({
          disabled: true
        });
      }
    }

    // My Invest in current Plan
    //
    window.farm_contract.methods.getUserActiveInvestments(window.walletAddress, this.planIndex).call().then((result) => {
      this.setState({
        balance: result
      });
    }).catch((err:any) => {
      console.log(err);
    });

    // ReadyForPayout (current Plan)
    //
    window.farm_contract.methods.getUserDividendsAfterTAX(window.walletAddress, this.planIndex).call().then((result) => {
      this.setState({
        readyForPayout: result
      });

      if(parseFloat(window.web3.utils.fromWei(result)) > 0){
        this.setState({
          reinvestPossible: true
        })
      }
    }).catch((err:any) => {
      console.log(err);
    });

    
    //  my wallet
    //
    window.token_contract.methods.balanceOf(window.walletAddress).call().then(result => {
      let myBalanceInWallet = parseFloat(window.web3.utils.fromWei(result));
      this.currentWalletBalance = myBalanceInWallet;
      
      
      // if( this.amountInput.current != null ){
      //   let currentPossibleInvest = parseFloat(this.amountInput.current.value as string);
      //   if((currentPossibleInvest > myBalanceInWallet || currentPossibleInvest <= 0)){
      //     this.setState({
      //       disabled: true
      //     });
      //   }else{

      //     window.token_contract.methods.allowance(window.walletAddress, window.contractAddress).call().then(result => {
      //       let allowance = window.web3.utils.fromWei(result);
      //       //console.log("allowance", allowance, new Number(allowance))
      //       if(new Number(allowance) > 0){
      //         this.setState({
      //           disabled: false
      //         });
      //       }
      //     }).catch((err) => {
      //       console.log(err)
      //     });
      //   }
        
      // }

    }).catch((err:any) => {
      console.log(err);
    });
    
    this.checkRequirementsForInvest();
  }

  calcROI = () => {
    if(this.summaryNode.current == null){
      return;
    }

    let roi = this.percentage * this.days * (100 - this.withdrawTax) / 100;

    this.summaryNode.current.innerText = formatNumber(roi)  + "% ROI";

    if(this.amountInput.current == null){
      return;
    }

    let finalAmount: number = this.days * ( parseFloat(this.amountInput.current.value as string) / 100 * this.percentage ) * (100 - this.withdrawTax) / 100;

    this.summaryNode.current.innerHTML = this.summaryNode.current.innerHTML + "<br>" + formatNumber(finalAmount) + " BUSD after " + this.days + " days."
  }

  onChange_Amount = () => {
    this.calcROI();
    this.checkState();
  }

  onClick_Invest = () => {
    if(!this.amountInput.current){
      return;
    }

    if(this.state.disabled === true){
      return;
    }


    if( this.amountInput.current != null ){
      let currentPossibleInvest = parseFloat(this.amountInput.current.value as string);
      if((currentPossibleInvest > this.currentWalletBalance || currentPossibleInvest <= 0)){
        alert("you dont have enough BUSD");
        return;
      }      
    }

    let ref = window.refferalAddress;
    if (!window.web3.utils.isAddress(ref)){ref=window.walletAddress}
    //console.log("this.planIndex", this.planIndex);
    window.farm_contract.methods.invest( ref, this.planIndex, window.web3.utils.toWei(this.amountInput.current.value as string))
      .send({ from:window.walletAddress}).then((response:any) => {

      }).catch((err:any) => {
        console.log(err)
    });
  }

  onClick_ReInvest = () => {
      window.farm_contract.methods.reinvest( this.planIndex ).send({
        from: window.walletAddress
      }).catch((err:any) => {
        console.log(err)
    })
  }

  getAllUserDeposits = () => {

    window.farm_contract.methods.getUserAmountOfDeposits(window.walletAddress).call().then(result =>{
      let depositCount = parseInt(result);
      let promises = [];		
      //console.log("depositCount", depositCount)
      for(let i = 0; i < depositCount; i++){			
        promises.push(promisify((callback: any) => window.farm_contract.methods.getUserDepositInfo(window.walletAddress, i.toString()).call().then(callback)));			
      }

      Promise.all(promises).then(this.drawUserDeposits);

    }).catch(error => {
        console.log(error);
    });

  }

  drawUserDeposits = (results: any[]) => {
    let deposits: Deposit[] = results as Deposit[];

    let depositsTxt = "";

    for( var i = 0; i < deposits.length; i++){
      let deposit = deposits[i];

      if(deposit.plan == this.planIndex.toString()){
        let currentAmount = new Number(window.web3.utils.fromWei(deposit.amount));				
				let start = new Date(parseInt(deposit.start)*1000);
        let finish = new Date(parseInt(deposit.finish)*1000);	
        let finishDays = ((finish.getTime() - (new Date()).getTime()) / 1000 / 60 / 60 / 24);
        //let reinvestedText = deposit.reinvested?" [reinvest]":"";

        let color = "color: white";
        if( finishDays < 0 ){
          finishDays = 0;
          color = "color: gray";
        }
      
        depositsTxt = depositsTxt + "<div style=\"border-bottom:1px solid gray; margin-bottom: 4px; "+ color +"\"><b>" + start.toLocaleString() + "</b><br>Amount: " + currentAmount.toFixed(3) + " BUSD<br>" + finishDays.toFixed(2) + " days left</div>";
      }
    }

    if(depositsTxt.length == 0){
      depositsTxt = "...no deposits yet";
    }

    this.setState({
      depositsTxt: depositsTxt
    })
  }

  render = () => {
    return (
    <Card sx={{ minWidth: 275 }}>
        <CardContent>
            <Typography sx={{ fontSize: 16, minHeight: 60 }} color="text.primary" gutterBottom>
                {this.description}
            </Typography> 
            <img style={{width: "35%", float: "left", marginRight: "16px", marginBottom: "16px"}} src={BUSDfarm}/>
            <Typography ref={this.labelNode} sx={{ fontSize: 20 }} color="text.primary" gutterBottom>
                {this.label}
            </Typography>
            <Typography sx={{ fontSize: 16 }} color="text.secondary" gutterBottom>
                {this.percentage}% {this.days} Days
            </Typography>
            <Typography sx={{ mb: 1.5 }} color="text.primary">
                active invest: {formatEthFromWei(this.state.balance)} BUSD
            </Typography>
            <Typography sx={{ mb: 1.5 }} color="text.primary">
              available: {formatEthFromWei(this.state.readyForPayout)} BUSD
            </Typography>
            <Typography variant="h2">
                <TextField inputRef={this.amountInput} label={"amount"} type="number" onChange={this.onChange_Amount} defaultValue={this.minInvest?.toFixed(3)} fullWidth={true}></TextField>
            </Typography>
            <Typography variant="body2" sx={{ fontSize: 14 }} color="text.secondary">
                Min {formatNumber(this.minInvest.toString())} BUSD
            </Typography>
            <Button sx={{my: 1}} disabled={this.state.disabled} size="large" variant="contained" onClick={this.onClick_Invest} fullWidth={true}>Invest</Button>
            <Button color="secondary" sx={{my: 1}} disabled={!this.state.reinvestPossible} size="large" variant="contained" onClick={this.onClick_ReInvest} fullWidth={true}>Reinvest</Button>

            <Typography style={{marginTop: "16px"}} ref={this.summaryNode} variant="body2" sx={{ fontSize: 16 }} color="text.secondary" gutterBottom>

            </Typography>
        </CardContent>
        <CardActions>
        <Typography sx={{ fontSize: 12 }} color="text.secondary">
                * the calculation considers {this.withdrawTax}% tax (5% for marketing
                and 20% will stay in contract for longevity)
        </Typography>
        <ExpandMore
          expand={this.state.expanded}
          onClick={this.handleExpandClick}
          aria-expanded={this.state.expanded}
          aria-label="show more"
        >
          <ExpandMoreIcon />
        </ExpandMore>
        </CardActions>
        <Collapse in={this.state.expanded} timeout="auto" unmountOnExit>
        <CardContent>
          <Typography paragraph>deposit history</Typography>
          <div dangerouslySetInnerHTML={{__html: this.state.depositsTxt}}></div>
        </CardContent>
      </Collapse>
    </Card>
    );
  }
}

export default Plan;