import React from 'react';
import { Button } from '@mui/material';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import { Grid } from '@mui/material';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import { GetUserInfoResponse } from '../assets/abi/farm-contract';

import { formatEthFromWei } from '../Formatters';
import Web3 from 'web3';



interface UserStatsProps {
  style?: React.CSSProperties;
}

const bull = (
    <Box
      component="span"
      sx={{ display: 'inline-block', mx: '2px', transform: 'scale(0.8)' }}
    >
      •
    </Box>
  );

class UserStats extends React.Component<UserStatsProps> {

  state = {
    userInfo: { totalDeposit: "0", totalReferrals: "0", totalWithdrawn: "0"},
    balance: "",
    referralBonus: "",
    availableForWithdraw: "",
    timeLeftUntilFull: ""
  }
  baseState: any;
  _stateInterval: any;

  private myBalanceNode: React.RefObject<HTMLSpanElement>;

  constructor(props: UserStatsProps) {
      super(props);

      this.baseState = this.state;

      this.myBalanceNode = React.createRef();


      // properties
  }

  componentDidMount = () => { 

    this.checkState();
    if(this._stateInterval){
      clearInterval(this._stateInterval);
    }
    this._stateInterval = setInterval(this.checkState, window.dataRefreshInterval + (window.dataRefreshInterval / 100 * 40 /*40% slower*/));
  }

  componentWillUnmount = () => { 

  }

  onClick_Withdraw = () => {
    window.farm_contract.methods.withdraw().send({
      from: window.walletAddress
    }).catch((err: any) => {
        console.log(err);
    });
  }

  checkState = () => {  
    if(window.connected !== true){
      this.setState(this.baseState);
      return;
    }
    window.farm_contract.methods.getUserInfo(window.walletAddress).call().then((pUserInfo: GetUserInfoResponse) => {
      this.setState({
        userInfo: pUserInfo
      });
    }).catch((err:any) => {
      console.log(err);
    });

    window.token_contract.methods.balanceOf(window.walletAddress).call().then(result => {
      this.setState({balance: result});
    }).catch((err:any) => {
      console.log(err);
    });

    window.farm_contract.methods.getUserReferralBonusAfterTax(window.walletAddress).call().then((result: string) => {
      this.setState({
        referralBonus: result
      });
    }).catch((err:any) => {
      console.log(err);
    });

    window.farm_contract.methods.getUserAvailable(window.walletAddress).call().then((result: string) => {

      let dividens = parseFloat(Web3.utils.fromWei(result));

      this.setState({
        availableForWithdraw: new Intl.NumberFormat('en-GB', {maximumSignificantDigits: 2}).format(dividens)
      });

    }).catch((err:any) => {
      console.log(err);
    });

    window.farm_contract.methods.getUserCutoff(window.walletAddress).call().then((result: string) => {
      let cutOffDate = new Date(parseInt(result)*1000);
      let hoursLeft = (cutOffDate.getTime() - new Date().getTime()) / 1000 / 60 / 60 // milliseconds to hours;
      let timeLeftText = new Intl.NumberFormat('en-GB').format(hoursLeft);
      if( hoursLeft <= 0 ){
        timeLeftText = "0";
      }

      this.setState({
        timeLeftUntilFull: timeLeftText
      });
    }).catch((err:any) => {
      console.log(err);
    });

  }  

  render = () => {
    return (
    <Card sx={{ minWidth: 275 }}>
        <CardContent>
          <Grid container>
            <Grid item xs={12} md={6}>
              <Typography sx={{ fontSize: 16 }} color="text.secondary" gutterBottom>
                my wallet
              </Typography>
              <Typography ref={this.myBalanceNode} sx={{ fontSize: 20 }} color="text.primary" gutterBottom>
              {formatEthFromWei(this.state.balance)} BUSD
              </Typography>
            </Grid>

            <Grid item xs={12} md={6}>
              <Typography sx={{ fontSize: 16 }} color="text.secondary" gutterBottom>
                  deposits
              </Typography>
              <Typography sx={{ fontSize: 20 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.userInfo.totalDeposit)} BUSD
              </Typography>
            </Grid>

            <Grid item xs={12} md={6}>
              <Typography sx={{ fontSize: 16 }} color="text.secondary" gutterBottom>
                  withdrawn
              </Typography>
              <Typography sx={{ fontSize: 20 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.userInfo.totalWithdrawn)} BUSD
              </Typography>
            </Grid>

            <Grid item xs={12} md={6}>
              <Typography sx={{ fontSize: 16 }} color="text.secondary" gutterBottom>
                Referral Bonus
              </Typography>
              <Typography sx={{ fontSize: 20 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.referralBonus)} BUSD
              </Typography>
            </Grid>

            <Grid item xs={12} md={6}>
              <Typography sx={{ fontSize: 16 }} color="text.secondary" gutterBottom>
                available for withdraw
              </Typography>
              <Typography sx={{ fontSize: 20 }} color="text.primary" gutterBottom>
                  {this.state.availableForWithdraw} BUSD
              </Typography>
            </Grid>

            <Grid item xs={12} md={6}>
              <Typography sx={{ fontSize: 16 }} color="text.secondary" gutterBottom>
                claim within *
              </Typography>
              <Typography sx={{ fontSize: 20 }} color="text.primary" gutterBottom>
                  {this.state.timeLeftUntilFull} hours
              </Typography>
            </Grid>

            <Grid item xs={12} md={6}>
              <Button sx={{my: 1}} size="large" variant="contained" onClick={this.onClick_Withdraw}>Withdraw</Button>
            </Grid>

            <Grid item xs={12} md={6}>
              <Typography sx={{ fontSize: 12 }} color="text.secondary" gutterBottom>
              * everyone has to pay out their profits within this period. if the timer goes to 0, the profits will not continue to increase until a payout or reinvest takes place
              </Typography>
            </Grid>
          </Grid>
        </CardContent>
        <CardActions>

        </CardActions>
    </Card>
    );
  }
}

export default UserStats;