import React from 'react';
import logo from './logo.svg';
import './App.css';

import { styled } from '@mui/material/styles';
import { Button, Grid, Typography } from '@mui/material';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Plan from './components/Plan';
import UserStats from './components/UserStats';
import ContractStats from './components/ContractStats';
import { AbiItem } from 'ethereum-abi-types-generator';

import ConnectButton from "./components/ConnectButton";

import busdfarm from "./assets/img/busd-logo-medium.png";
import busdfarmtext from "./assets/img/busdfarm-header-text.png";
import bnbfarm from "./assets/img/bnbfarm-glow.png";

import {ContractContext} from './assets/abi/farm-contract';
import {ContractContext as TokenContractContext} from './assets/abi/busd';
import Web3 from "web3";
import copy from 'clipboard-copy';

//
//  Global setup Web3 Contract-Interaction
//
import abi from "./assets/abi/farm-contract.json";
import tokenAbi from "./assets/abi/busd.json";
import { Opacity } from '@mui/icons-material';

// const contractAddress: string = '0x4C0290B5Add53Ff9b5587CF101B703574f7bB721'; // release on testnet
// const tokenAddress: string = '0x78867BbEeF44f2326bF8DDd1941a4439382EF2A7'; // release on testnet

const contractAddress: string = '0x4e57f68b3d38b6413de223ea2d613e2c756307d5'; // release on mainnet
const tokenAddress: string = '0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56'; // release on mainnet


declare global {
  interface Window {
    dataRefreshInterval: number,
    farm_contract: ContractContext;
    token_contract: TokenContractContext;
    web3: Web3;
    ethereum: any;
    contractAddress: string;
    contract_abi: AbiItem[]
    tokenAddress: string;
    token_abi: AbiItem[];
    walletAddress: string;
    connected: boolean;
    refferalAddress: string; // the ref address that invited me
  }
}

interface AppProps {
  style?: React.CSSProperties;
}

const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.h3,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

const modalStyle = {
  position: 'absolute' as 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: "70%",
  height: "60%",
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
  color: "lightgrey",
  overflow: "auto",
  opacity: 0.95
};

window.dataRefreshInterval = 5000;

class App extends React.Component<AppProps> {
  
  state = {
    modalOpen: false
  }

  constructor(props: AppProps) {
    super(props);

    // properties
  }

  private _modalInterval: any = null;

  componentDidMount = () => { 
    window.contractAddress = contractAddress;
    window.tokenAddress = tokenAddress;
    window.contract_abi = abi as AbiItem[];
    window.token_abi = tokenAbi as AbiItem[];
    
  
    const urlParams = new URLSearchParams(window.location.search);
    let refAddress = urlParams.get('ref');
    if(refAddress != null){
      window.refferalAddress = refAddress;
    }    

    let unlock = urlParams.get('unlock');
    if(unlock != "true"){
      this._modalInterval = setInterval(this.handlePhase2Modal, 1000);
    }    
  }

  componentWillUnmount = () => { 
    
  }

  handlePhase2Modal = () => {
    let countDownDateUTC = new Date();
    countDownDateUTC.setUTCDate(18);
    countDownDateUTC.setUTCMonth(10);
    countDownDateUTC.setUTCFullYear(2021);
    countDownDateUTC.setUTCHours(14);
    countDownDateUTC.setUTCMinutes(0);
    countDownDateUTC.setUTCSeconds(0);

    if((new Date()).getTime() < countDownDateUTC.getTime()){
      this.setModalOpen();
    }else{
      if(this._modalInterval != null ){
        clearInterval(this._modalInterval);
        this._modalInterval = null;
      }
    }
  }

  setModalOpen = () => {
    this.setState({modalOpen: true});
  }

  setModalClose = () => {
    clearInterval(this._modalInterval);
    this._modalInterval = null;
    this.setState({modalOpen: false});
  }

  copyRefToClipboard = () => {
    let refAddress = window.location.origin + "?ref=" + window.walletAddress;
    copy(refAddress);
  }

  onClick_MyBnbFarm = () => {
    let a = document.createElement("a");
    a.target = "_blank";
    a.href = "https://mybnbfarm.com";
    a.click();
  }

  //  Main Layout
  //
  render = () => {
    return (
      <Box sx={{ flexGrow: 1 }} style={{width: "80%", margin: "auto", marginTop: "32px"}}>
        <Grid id="mainApp" container spacing={2}>
          <Grid item xs={12}>
            {/* <Item style={{textAlign: "left"}}><img style={{width: "100%"}} src={headerbg}/></Item> */}
            <Item sx={{ display: { xs: 'block', md: 'none' } }} className={"flex-middle-center"}>
              <div className={"flex-middle-center"}>
                <img style={{width: "100%"}} src={busdfarmtext}/>
              </div>
            </Item>
            <Item sx={{ display: { xs: 'none', md: 'block' } }}>
              <div className={"flex-middle-center"}>
                <img style={{width: "10%"}} src={busdfarm}/>
                <img style={{width: "80%"}} src={busdfarmtext}/>
              </div>
            </Item>
          </Grid>
          <Grid item xs={12} md={3}>
            <Paper style={{padding: "16px"}}>
              <ConnectButton />

              <Typography sx={{ fontSize: 16 }} color="text.secondary" gutterBottom>
                referral link
              </Typography>
              <Typography sx={{ fontSize: 16 }} style={{wordBreak: "break-all"}} color="text.primary" gutterBottom>
                {window.location.origin}?ref=<span id={"walletAddressRefLink"}></span><br/>
                <Button color={"secondary"} variant={"contained"} sx={{fontSize: 12}} onClick={this.copyRefToClipboard}>copy</Button>              
              </Typography>
              <Typography sx={{ fontSize: 12 }} color="text.secondary" gutterBottom>
                For the friends that you invite you receive 5% of their deposits.
              </Typography>
            </Paper>
          </Grid>
          <Grid item xs={12} md={3}>
            <ContractStats></ContractStats>
          </Grid>
          <Grid item xs={12} md={3}>
            <UserStats></UserStats>
          </Grid>
          <Grid item xs={12} md={3}>
            <Paper style={{padding: "16px"}}>
              <Button sx={{my: 1}} onClick={this.onClick_MyBnbFarm}><img style={{width: "60%", float: "left"}} src={bnbfarm}/></Button>
              <br/>go to MyBnbFarm.com
            </Paper>
          </Grid>
          <Grid item xs={12} md={3}>
            <Plan label={"Plan 1"} description={"Invest in this plan to activate the VIP plans"} minInvest={10} percentage={7} days={30} planIndex={0} nonVipMinInvest={0}></Plan>
          </Grid>
          <Grid item xs={12} md={3}>
            <Plan label={"Plan 2"} description={"Invest in this plan to activate the VIP plans"} minInvest={10} percentage={5} days={50} planIndex={1} nonVipMinInvest={0}></Plan>
          </Grid>
          <Grid item xs={12} md={3}>
            <Plan label={"VIP HUSTLER"} description={"Enable this plan by investing at least $750 in Plan 1 or 2"} minInvest={1500} percentage={23} days={7}  planIndex={2} nonVipMinInvest={750}></Plan>
          </Grid>
          <Grid item xs={12} md={3}>
            <Plan label={"VIP BALLER"} description={"Enable this plan by investing at least $1,500 in Plan 1 or 2"} minInvest={3000} percentage={32} days={5}  planIndex={3} nonVipMinInvest={1500}></Plan>
          </Grid>
          {/* <Grid item xs={12} md={12}>
            <PlanDynamic label={"Dynamic"} percentage={7} days={20}></PlanDynamic>
          </Grid> */}

          <Grid item xs={12} md={12}>
            <Paper style={{padding: "16px"}}>
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
              Select your plan and Invest, then withdraw earnings at least every 48 hours. The principal deposit cannot be withdrawn, only the retuns and referral rewards are paid out. Check the Transactions on Contract and Contract Source. High Risk and High Rewards! DYOR!
              </Typography>
            </Paper>
          </Grid>
          <Grid item xs={12} md={12}>
            <Paper style={{padding: "16px"}}>
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                A 10% deposit fee is paid to the project/marketing wallet but this fee is not paid by you as a user, this fee is paid by the contract as a whole. your investment will get to the plan deposits 1:1. <br/>
                A 5% withdraw/reinvest fee is payed to the project/marketing wallet. this fee is also paid by the contract as a whole. <br/>
                20% of every withdraw will stay in the contract to allow longevity. this is not a tax that is used for marketing. it simply avoids the fast drain of the contract and solidifies the pool for withdrawls of all investors.<br/>
                <b>the displayed values on mybusdfarm are after tax. you will get what you see. the ROI value of each Plan is accurate.</b><br/>
                We are using this fees to pay marketing, developers and all other needs of this project.<br/>
              </Typography>
            </Paper>
          </Grid>
        </Grid>
        

        <Modal
          open={this.state.modalOpen}
         /*  onClose={this.setModalClose} */
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
          disableEscapeKeyDown={true}

        >
          <Box sx={modalStyle}>
            <Typography id="modal-modal-title" variant="h6" component="h2">
            🔔 we are not live yet! 🔔
            </Typography>
            <Typography id="modal-modal-description" sx={{ mt: 2, fontSize: 14 }}>
stay tuned
            </Typography>
            {/* <Button sx={{my: 1}} size="small" fullWidth={true} variant={"contained"} onClick={this.setModalClose}>close</Button> */}
          </Box>
        </Modal>
      </Box>
    );
  }
}

export default App;
